* {
    font-family: Helvetica, Arial, sans-serif;

}

body {
    background-color: #21232A;
    color: white;
    padding: 60px;
}

input {
    color: white;
    background-color: rgb(69, 69, 78);
    font-size: 1.2rem;
    width: 100%;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}


.card {
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 170px;   
}